import { useEffect, useReducer, useState } from "react";

import { initialState, reducer } from "store/store";

import Plane from "components/Plane";
import Bullet from "components/Bullet";
import Rock from "components/Rock";

import "./GameMap.css";
import Explosion from "components/Explosion";
import useInterval from "hooks/useInterval";
import GameOver from "views/GameOver";

const GameMap = ({ changeEndGame }) => {
  const [state, dispatch] = useReducer(reducer, initialState);

  const [movePlayer, setMovePlayer] = useState("none");

  const Player = {
    xMove: "none",
    shoot: false,
  };

  function drawFn() {
    if (movePlayer === "right") {
      dispatch({ type: "MOVE_RIGHT" });
    }
    if (movePlayer === "left") {
      dispatch({ type: "MOVE_LEFT" });
    }
    dispatch({ type: "MOVE_ROCK" });
    dispatch({ type: "MOVE_BULLET" });
    dispatch({ type: "DESTRUCTION_ROCK" });
    dispatch({ type: "LOSS_LIFE" });
  }

  function newRock() {
    dispatch({ type: "NEW_ROCK" });
  }

  function setSizeWindow() {
    const windowsSizeX = window.innerWidth;
    const windowsSizeY = window.innerHeight;

    dispatch({
      type: "SET_WINDOW_SIZE",
      sizeX: windowsSizeX,
      sizeY: windowsSizeY,
    });
  }

  function movePlane() {
    window.addEventListener("keydown", (e) => {
      const key = e.keyCode;

      switch (key) {
        case 37:
          Player.xMove = "left";
          setMovePlayer("left");
          break;

        case 39:
          Player.xMove = "right";
          setMovePlayer("right");
          break;
        case 32:
          if (Player.shoot !== true && state.GameOver === false) {
            Player.shoot = true;
            dispatch({ type: "NEW_BULLET" });
          }
          break;
        default:
          break;
      }
    });

    window.addEventListener("keyup", (e) => {
      const key = e.keyCode;
      switch (key) {
        case 37:
          Player.xMove = "none";
          setMovePlayer("none");
          break;

        case 39:
          Player.xMove = "none";
          setMovePlayer("none");
          break;
        case 32:
          Player.shoot = false;

          break;

        default:
          break;
      }
    });
  }

  useEffect(() => {
    movePlane();
    setSizeWindow();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useInterval(
    () => {
      drawFn();
    },
    !state.GameOver ? 1000 / 60 : null
  );

  useInterval(
    () => {
      newRock();
    },
    !state.GameOver ? 2000 : null
  );

  const MapBullet = state.Bullet.map(({ id, posX, posY }) => {
    return <Bullet key={id} id={id} posX={posX} posY={posY} />;
  });

  const MapRock = state.Rock.map(({ id, posX, posY }) => {
    return <Rock key={id} id={id} posX={posX} posY={posY} />;
  });
  const MapExplosion = state.Explosion.map(({ id, posX, posY }) => {
    return <Explosion key={id} id={id} posX={posX} posY={posY} />;
  });

  return (
    <div className="map">
      <Plane positionX={state.PlayerX} />

      {state.Bullet.length > 0 && MapBullet}
      {state.Rock.length > 0 && MapRock}

      {state.Explosion.length > 0 && MapExplosion}
      <div className="game-info">
        <p className="player-hp">
          <span>{state.HP_Player}</span> HP
        </p>
        <p className="player-points">
          <span>SCORE: {state.Points}</span>
        </p>
      </div>
      <div className="floor"></div>
      {state.GameOver && (
        <GameOver score={state.Points} changeEndGame={changeEndGame} />
      )}
    </div>
  );
};

// 960 / 768
// 192

export default GameMap;
