import { useReducer } from "react";
import { initialState, reducer } from "store/store";

import "./Root.css";

import GameMap from "views/GameMap";
import StartGame from "views/StartGame";

function Root() {
  const [state, dispatch] = useReducer(reducer, initialState);

  function changeStartGame() {
    dispatch({ type: "SET_START_GAME" });
  }
  function changeEndGame() {
    dispatch({ type: "SET_END_GAME" });
  }

  return (
    <div className="App">
      <header className="App-header">
        {state.StartGame ? (
          <GameMap changeEndGame={changeEndGame} />
        ) : (
          <StartGame changeStartGame={changeStartGame} />
        )}
      </header>
    </div>
  );
}

export default Root;
